import { useLocalStorage } from '@uidotdev/usehooks'
import React, { useEffect } from 'react'
import { useParams } from 'react-router-dom'
import Button from 'react-bootstrap/Button';
import Badge from 'react-bootstrap/Badge';
import Card from 'react-bootstrap/Card';
import Table from 'react-bootstrap/Table'
import NoResults from './NoResults';
import status from './helpers.js/statusStyle';
import {Clock, getTimePassed} from './helpers.js/dateAndTime';


function Job() {
    const {id} = useParams()
    const [db_jobs, saveJob] = useLocalStorage('jobs')
    var toggleNoResult = false
    const job = (db_jobs.filter((job) => ( job.id == id )).length == 0)? toggleNoResult = true: db_jobs.filter((job) => ( job.id == id ))[0]
    const otherJobs = (db_jobs.filter((job) => job.id != id))

    const kohaPranimit = `${job.date} ${job.time}`
    const kohaMomentale = `${Clock().date} ${Clock().time}`
    const elapsedTime = getTimePassed(kohaPranimit, kohaMomentale)
    

    console.log();

    const handlePerfunduar = () =>{
        const updatedDb = db_jobs.map((item) => (item.id === job.id) ? { ...item, status: 'prfunduar' } : item);
        saveJob(updatedDb)
    }
    const handleNeProces = () =>{
        const updatedDb = db_jobs.map((item) => (item.id === job.id) ? { ...item, status: 'n proces' } : item);
        saveJob(updatedDb)

    }

    const handleGati = () =>{
        const updatedDb = db_jobs.map((item) => (item.id === job.id) ? { ...item, status: 'gati' } : item);
        saveJob(updatedDb) 
    }
    const handleNukRregullohet= () =>{
        const updatedDb = db_jobs.map((item) => (item.id === job.id) ? { ...item, status: 'nuk rregullohet' } : item);
        saveJob(updatedDb) 
    }
 
    // me ndrru statusin e jobit permes tastieres (me shti ne useEffect i bojke duplicate)   
        const handleKeyPress = (e) =>{
            const key = e.key
            if(key == 'Enter'){
                handlePerfunduar()
            }
            if(key == 'g'){
                handleGati()
            }
            if(key == 'p'){
                handleNeProces()
            }
            if(key == 'x'){
                handleNukRregullohet()
            }
           
        }

        window.addEventListener('keydown', handleKeyPress)

    if (toggleNoResult == false){
            return (
        <div className='mt-3'>
            <Card>
                <Card.Header className='d-flex justify-content-between align-items-center'>
                    Detajet e puns s pranuar.
                    <a href={`/print/${job.id}`}className='btn btn-outline-primary'>
                        <i className="bi bi-printer"></i>
                    </a>
                </Card.Header>
                <Card.Body>
                <Card.Title>{job.name}<Badge bg={status(job.status)}>{job.status}</Badge></Card.Title>
                    <Card.Text className='text-secondary'>
                        {job.date} {job.time}
                    </Card.Text>
                    <Card.Text>
                        <b>Pershkrimi:</b> {job.desc}
                    </Card.Text>
                    <Table striped bordered hover className=' mt-3'>
                        <thead>
                            <tr>
                                <th>#</th>
                                <th>Kodi</th>
                                <th>Kontakti</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>{job.id}</td>
                                <td>{job.kodi}</td>
                                <td>{job.kontakt}</td>
                            </tr>
                        </tbody>
                    </Table>
                <div className='d-flex flex-column align-items-start'>
                    <h4>Dokumento statusin</h4>
                    <div className='d-flex flex-row justify-content-between w-100'>
                        <div className='d-inline'>
                        <Button variant="warning" className='mx-1' onClick={handleNeProces}>N Proces</Button>
                        <Button variant="primary" className='mx-1' onClick={handleGati}>Gati</Button>
                        <Button variant="success" className='mx-1' onClick={handlePerfunduar}>Perfunduar</Button>
                        </div>
                        <Button variant="danger" onClick={handleNukRregullohet}>Nuk rregullohet</Button>
                    </div>
                </div>
                </Card.Body>
                <Card.Footer>
                <small className="text-muted">{elapsedTime}</small>
                </Card.Footer>
            </Card>
        </div>
    )
    }else{
        return (
            <NoResults/>
        )
    }
  
}

export default Job