import React, { useState } from 'react'
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import { useLocalStorage } from "@uidotdev/usehooks";
import Barcode from 'react-barcode'
import ReactToPrint from 'react-to-print'



function SettingsComp() {
    const [settings, setSettings] = useLocalStorage('settings', [])
    const [kuHeight, setKuHeight] = useState(25)
    const [kuWidth, setKuWidth] = useState(40)

    const handleSubmit = (e) =>{
        e.preventDefault()
        const width = e.target[0].value
        const height = e.target[1].value

        const print2 = e.target[2].checked

        setSettings({
            paperWidth: width,
            paperHeight: height,
            print2pages: print2
        })
        alert('Ndryshimi u b me sukses')
        
    }

    const updateHeight = (e)=>{
        setKuHeight(e.target.value)
    }   
    const updateWidth = (e)=>{
        setKuWidth(e.target.value)
    }

    return (
        <div className='mt-5 border p-4 rounded'>
            <Form  onSubmit={handleSubmit}>
                <Row className="mb-3">
                    <Form.Group as={Col} controlId="width">
                    <Form.Label>Gjersia (mm)</Form.Label>
                    <Form.Control type="number" placeholder="40mm" onKeyUp={updateWidth}/>
                    </Form.Group>
                                
                    <Form.Group as={Col} controlId="height">
                    <Form.Label>Lartsia (mm)</Form.Label>
                    <Form.Control type="number" placeholder="25mm" onKeyUp={updateHeight}/>
                    </Form.Group>
                </Row>
                <Form.Group className="mb-3" id="formGridCheckbox">
                    <Form.Check type="checkbox" label="Printo dy kopje" />
                </Form.Group>
                <Button variant="primary" type="submit">
                    Ruaj
                </Button>    
            </Form>
            <div className='preview mt-4'>
                <div className='label border d-flex flex-column align-items-center justify-content-between p-1 rounded' style={{width: `${(kuWidth * 3.8)}px`, height: `${(kuHeight * 3.8)}px`}}>
                    <div className='w-100'>
                        <p className='m-0' style={{fontSize: '12px'}}>11/17/2023, 1:40</p>
                        <p className='m-0' style={{fontSize: '12px'}}>Emri Klientit</p>
                    </div>
                    <Barcode width={1.5} height={30} value={'75568'} fontSize={7} margin={6}/>
                </div>
            </div>
            
        </div>
      );
}

export default SettingsComp